<template>
    <div>
        <div class="container-body">
            <div>
                <div class="weui-cell">
                    <div class="weui-cell__bd">
                        <input class="weui-input" v-model="mobile" type="text" placeholder="" />
                    </div>
                </div>
                <div class="weui-cell">
                    <div class="weui-cell__bd">
                        <input class="weui-input" v-model="password" type="password" placeholder="6-20" />
                    </div>
                </div>
                <div class="weui-cell">
                    <div class="weui-cell__bd">
                        <input class="weui-input" id="region" v-model="region" type="text" @click="selectRegion" placeholder="" />
                    </div>
                </div>
                <div class="weui-cell weui-cell_vcode">
                    <div class="weui-cell__bd">
                        <input class="weui-input" type="text" v-model="code" maxlength="6" placeholder="">
                    </div>
                    <div class="weui-cell__ft">
                        <a href="javascript:;" class="weui-vcode-btn" disabled="true" @click="getCode"></a>
                    </div>
                </div>
                <div class="weui-cell"></div>
                <a href="javascript:;" class="weui-btn weui-btn_primary" @click="registered"></a>
                <label for="weuiAgree" class="weui-agree">
                    <input id="weuiAgree" type="checkbox" class="weui-agree__checkbox">
                    <span class="weui-agree__text">
                        <a href="javascript:void(0);"></a>
                    </span>
                </label>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    components: {},
    data() {
        return {
            mobile: '',
            password: '',
            code: '',
            region: '',
            regionList: [],
            region_code: 0
        }
    },
    mounted() {
        $("#region").focus(function() {
            document.activeElement.blur();
        });
        var param = {
            c: 'Zb',
            m: 'Region',
            a: 'getRegionList',
            mapping: {
                name: 'label',
                code: 'value'
            }
        };
        var p_obj = {
            action: '',
            param: param,
            success: (response) => {
                this.regionList = response;
            },
            fail: (response) => {
                weui.alert(response.msg)
            }
        };
        AjaxHelper.GetRequest(p_obj);
    },
    methods: {
        selectRegion() {
            var _self = this;
            weui.picker(this.regionList, {
                defaultValue:[2],
                className: 'custom-classname',
                onConfirm: function(result) {
                    _self.region_code = result[2].value;
                    _self.region = result[0].label + " " + result[1].label + " " + result[2].label
                },
                id: 'doubleLinePicker'
            });
        },
        getCode() {
            var param = {
                mobile: this.mobile
            };
            if(!(/^1(3|4|5|7|8)\d{9}$/.test(param.mobile))){ 
                weui.alert("");  
                return false; 
            };
            var p_obj = {
                action: 'c=Zb&m=User&a=sendMobileCode',
                param: param,
                success: (response) => {

                },
                fail: (response) => {
                    alert(response.msg)
                }
            };
            AjaxHelper.PostRequest(p_obj);
        },
        registered() {
            var param = {
                account: this.mobile,
                password: this.password,
                code: this.code,
                region_code: this.region_code
            };
            var p_obj = {
                action: 'c=Zb&m=Register&a=registerByMobile',
                param: param,
                success: (response) => {
                    this.$router.push({
                        path: 'login'
                    });
                },
                fail: (response) => {
                    weui.alert(response.msg)
                }
            };
            AjaxHelper.PostRequest(p_obj);
        }
    },
    destroyed() {}
}
</script>
<style lang="less" scoped>
.container-body {
    margin-top: 20px;
    .weui-flex,
    .weui-btn {
        margin: 0 15px;
    }
    .weui-btn {
        height: 45px;
        background: #A877E6;
        box-shadow: 0 4px 9px 0 rgba(178, 125, 227, 0.80);
        border-radius: 4px;
    }
    .flex-item {
        font-size: 14px;
        color: #4990E2;
    }
    .weui-agree__checkbox {
        width: 16px;
        height: 16px;
    }
    .weui-agree {
        font-size: 16px;
    }
}
</style>



